import React, { Dispatch, useContext, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { AnyAction } from '@reduxjs/toolkit';
import { NativeScrollEvent, NativeSyntheticEvent, Platform } from 'react-native';
import { useNavigation } from '@react-navigation/native';
import { DrawerNavigationProp } from '@react-navigation/drawer';
import { PasswordList, Props as RenderProps } from 'screens/PasswordList/passwordList';
import {
  resetModal,
  resetSnackbar,
  selectModalMessage,
  selectModalTitle,
  selectModalVisible,
  selectSnackbarMessage,
  selectSnackbarVisible,
  setModalMessage,
  setModalTitle,
  setModalVisible,
} from 'reduxStore/slices/uiElementsSlice';
import {
  deletePasswordFromFirebase,
  editPasswordFromFirebase,
  getPasswordsFromFirebase,
  selectIsDeleteMode,
  selectIsEditMode,
  selectPasswords,
  setIsCreateMode,
} from 'reduxStore/slices/passwordSlice';
import usePasswordsFromFirebase from 'hooks/usePasswordsFromFirebase';
import { selectRequest } from 'reduxStore/slices/applicationStatusSlice';
import { TranslationsContext } from 'contexts/translationsContext';
import { PasswordI } from 'utils/constants';
import { setPasswordList } from 'utils/localStorageFuncs';

const PasswordListContainer = ({ children }: { children: typeof PasswordList }) => {
  const [scrollIsClose2Bottom, setScrollIsClose2Bottom] = useState<boolean>(false);
  const [refreshing, setRefreshing] = useState(false);
  const [searchText, setSearchText] = useState<string>('');
  const { modalTranslations } = useContext(TranslationsContext);
  const navigation: DrawerNavigationProp<any> = useNavigation();
  const dispatch: Dispatch<AnyAction> = useDispatch();

  const snackbarVisible: boolean = useSelector(selectSnackbarVisible);
  const snackbarMessage: string = useSelector(selectSnackbarMessage);
  const modalTitle: string = useSelector(selectModalTitle);
  const modalMessage: string = useSelector(selectModalMessage);
  const modalVisible: boolean = useSelector(selectModalVisible);
  const isEditMode: boolean = useSelector(selectIsEditMode);
  const isDeleteMode: boolean = useSelector(selectIsDeleteMode);
  const passwords: Array<PasswordI> = usePasswordsFromFirebase();
  const passwordsFromState: Array<PasswordI> = useSelector(selectPasswords);
  const [passwordsFiltered, setPasswordsFiltered] = useState<Array<PasswordI>>(passwords);
  const isRequesting: boolean = useSelector(selectRequest);

  useEffect(() => {
    setPasswordsFiltered(passwords);

    if (passwords.length > 0) {
      const somePwdIsNotEncrypted = passwords.some(
        ({ pwdWasCorrectlyEncrypted }) => !pwdWasCorrectlyEncrypted
      );

      if (somePwdIsNotEncrypted) {
        dispatch(setModalTitle({ modalTitle: modalTranslations.t('modalTitle') }));
        dispatch(setModalMessage({ modalMessage: modalTranslations.t('modalMessage') }));
        dispatch(setModalVisible({ modalVisible: true }));
      }
    }
  }, [passwords]);

  const handleRefresh = () => {
    setRefreshing(true);

    setTimeout(() => {
      setPasswordList([]);
      dispatch(getPasswordsFromFirebase());
      setPasswordsFiltered(passwordsFromState);
      setRefreshing(false);
    }, 800);
  };

  const handleScrollIsClose2Bottom = (event: NativeSyntheticEvent<NativeScrollEvent>) => {
    const { layoutMeasurement, contentOffset, contentSize } = event.nativeEvent;
    const paddingToBottom = Platform.OS === 'web' ? 7 : 10;
    const result =
      layoutMeasurement.height + contentOffset.y >= contentSize.height - paddingToBottom;

    setScrollIsClose2Bottom(result);
  };

  const handleOnDismissSnackbar = () => {
    dispatch(resetSnackbar());
  };

  const handleOnPressFAB = () => {
    dispatch(setIsCreateMode({ isCreateMode: true }));
    navigation.navigate('PasswordGenerator');
  };

  const handleModalToggle = () => {
    dispatch(resetModal());
  };

  const handleModalOnSubmit = () => {
    if (isDeleteMode) {
      dispatch(deletePasswordFromFirebase());
    } else if (isEditMode) {
      dispatch(editPasswordFromFirebase());
      navigation.navigate('PasswordList');
    }
    dispatch(resetModal());
  };

  const handleSearch = (text: string) => {
    setSearchText(text);
    if (text !== '') {
      const filtered = passwords.filter(({ description }) =>
        description.toLowerCase().includes(text.toLowerCase())
      );
      setPasswordsFiltered(filtered);
    } else {
      setPasswordsFiltered(passwords);
    }
  };

  const handleClearSearch = () => {
    setSearchText('');
    handleSearch('');
  };

  const snackbarObj = { snackbarVisible, snackbarMessage };
  const modalObj = { modalTitle, modalMessage, modalVisible };
  const handlers = {
    handleScrollIsClose2Bottom,
    handleOnDismissSnackbar,
    handleOnPressFAB,
    handleModalToggle,
    handleModalOnSubmit,
    handleSearch,
    handleClearSearch,
  };

  return children({
    navigation,
    snackbarObj,
    modalObj,
    scrollIsClose2Bottom,
    hasPasswordsStored: passwords.length > 4,
    passwordsFiltered,
    searchText,
    isRequesting,
    handlers,
    refreshControl: {
      refreshing,
      onRefresh: handleRefresh,
    },
  });
};

export default () => (
  <PasswordListContainer>
    {(renderProps: RenderProps) => <PasswordList {...renderProps} />}
  </PasswordListContainer>
);
