import React, { Dispatch, useEffect, useState } from 'react';
import * as Localization from 'expo-localization';
import { logger } from 'react-native-logs';
import { getOnBoardingViewed, getPasswordList } from 'utils/localStorageFuncs';
import { useDispatch, useSelector } from 'react-redux';
import {
  selectRequest,
  setIsRequest,
  setLocale,
  unsetIsRequest,
} from 'reduxStore/slices/applicationStatusSlice';
import { setPasswords } from 'reduxStore/slices/passwordSlice';
import { AnyAction } from '@reduxjs/toolkit';
import Root, { RootProps as RenderProps } from './root';

const RootContainer = ({ children }: { children: typeof Root }) => {
  const [loading, setLoading] = useState<boolean>(true);
  const isRequest = useSelector(selectRequest);
  const [onBoardingViewed, setOnBoardingViewed] = useState<boolean>(false);
  const dispatch: Dispatch<AnyAction> = useDispatch();
  const log = logger.createLogger();

  useEffect(() => {
    dispatch(setIsRequest());
    dispatch(setLocale({ locale: Localization.locale }));

    getOnBoardingViewed()
      .then((value) => {
        if (value === 'false' || value === undefined) {
          setOnBoardingViewed(false);
        } else {
          setOnBoardingViewed(true);
        }
      })
      .catch((err) => {
        log.error('Error checking is first launch: ', { err });
      })
      .finally(() => {
        setLoading(isRequest);
      });

    getPasswordList().then((passwords) => {
      dispatch(setPasswords({ passwords }));
    });
  }, []);

  return children({ loading, onBoardingViewed });
};

export default () => (
  <RootContainer>{(renderProps: RenderProps) => <Root {...renderProps} />}</RootContainer>
);
