import React, { Dispatch, useContext, useEffect, useState } from 'react';
import {
  CreateEditPasswordConfigurator,
  Props as RenderProps,
} from 'components/CreateEditPasswordConfigurator/createEditPasswordConfigurator';
import { useNavigation } from '@react-navigation/native';
import { useDispatch, useSelector } from 'react-redux';
import { AnyAction } from '@reduxjs/toolkit';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import { DrawerNavigationProp } from '@react-navigation/drawer';
import { selectSocialMediaList, setSocialMediaPicked } from 'reduxStore/slices/socialMediaSlice';
import {
  selectIsCreateMode,
  selectIsEditMode,
  selectPassword,
  selectPasswordIdPicked,
  selectPasswordPickedWasEncrypted,
} from 'reduxStore/slices/passwordSlice';
import usePasswordDescription from 'hooks/usePasswordDescription';
import useSocialMediaPicked from 'hooks/useSocialMediaPicked';
import handleCreateEditPassword from 'utils/handleCreateEditPassword';
import { TranslationsContext } from 'contexts/translationsContext';

const CreateEditPwdConfigurationContainer = ({ children }: ContainerProps) => {
  const navigation = useNavigation<DrawerNavigationProp<any> | NativeStackNavigationProp<any>>();
  const [description, setDescription] = useState<string>('');
  const { passwordGenTranslations, modalTranslations } = useContext(TranslationsContext);
  const dispatch: Dispatch<AnyAction> = useDispatch();
  const passwordDescriptionMaxLen = 60;

  const socialMediaPicked: string = useSocialMediaPicked();
  const passwordDescriptionPicked: string = usePasswordDescription();
  const passwordPickedWasEncrypted: boolean = useSelector(selectPasswordPickedWasEncrypted);

  const socialMediaList = useSelector(selectSocialMediaList);
  const isCreateMode: boolean = useSelector(selectIsCreateMode);
  const isEditMode: boolean = useSelector(selectIsEditMode);
  const passwordIdPicked: string = useSelector(selectPasswordIdPicked);
  const password: string = useSelector(selectPassword);

  const createEditButtonLabel = passwordGenTranslations.t(
    isCreateMode ? 'createButton' : 'editButton'
  );

  useEffect(() => {
    setDescription(passwordDescriptionPicked);
  }, [passwordDescriptionPicked]);

  const handleOnPress = () => {
    handleCreateEditPassword({
      dispatch,
      navigation,
      data: {
        isEditMode,
        isCreateMode,
        password,
        passwordIdPicked,
        description,
        socialMedia: socialMediaPicked,
      },
      modalTitle: modalTranslations.t('editModalTitle'),
      modalMessage: modalTranslations.t(
        !passwordPickedWasEncrypted ? 'editNonEncryptedModalMessage' : 'editModalMessage'
      ),
    });
  };

  const handleOnPickerChange = (itemValue: string) => {
    dispatch(setSocialMediaPicked({ socialMediaPicked: itemValue }));
  };

  const handlers = {
    handleOnPress,
    setDescription,
    handleOnPickerChange,
  };

  return children({
    description,
    passwordDescriptionMaxLen,
    socialMediaPicked,
    socialMediaList,
    isCreateMode,
    createEditButtonLabel,
    handlers,
  });
};

interface ContainerProps {
  children: typeof CreateEditPasswordConfigurator;
}

export default () => (
  <CreateEditPwdConfigurationContainer>
    {(renderProps: RenderProps) => <CreateEditPasswordConfigurator {...renderProps} />}
  </CreateEditPwdConfigurationContainer>
);
