// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-nocheck
import { call, put, takeLeading } from 'redux-saga/effects';
import { logger } from 'react-native-logs';

import {
  getSocialMediaListFromFirebase,
  setSocialMediaList,
} from 'reduxStore/slices/socialMediaSlice';
import { getSocialMedia } from 'services/database';
import { getSocialMediaList, setSocialMediaListToLocalStorage } from 'utils/localStorageFuncs';
import { SocialMediaI } from 'utils/constants';

const log = logger.createLogger();

function* getSocialMediaFlow(): Generator {
  try {
    const socialMediaListFromLocalStorage: Array<SocialMediaI> = yield call(getSocialMediaList);

    if (socialMediaListFromLocalStorage.length === 0) {
      const socialMediaList = yield call(getSocialMedia);

      yield call(setSocialMediaListToLocalStorage, socialMediaList);
      yield put(setSocialMediaList({ socialMediaList }));
    }

    yield put(setSocialMediaList({ socialMediaList: socialMediaListFromLocalStorage }));
  } catch (exception) {
    log.error('GetSocialMediaSaga: ', { exception: exception.message });
  }
}

export const getSocialMediaSaga = [
  takeLeading(getSocialMediaListFromFirebase.type, getSocialMediaFlow),
];
