import React, { useContext } from 'react';
import { Dimensions, Platform, TouchableOpacity } from 'react-native';
import { createDrawerNavigator, DrawerContentComponentProps } from '@react-navigation/drawer';
import { Feather } from '@expo/vector-icons';
import PasswordList from 'screens/PasswordList/passwordListContainer';
import CustomSidebar from 'components/CustomSidebar/customSidebarContainer';
import { TranslationsContext } from 'contexts/translationsContext';
import { appColors } from 'utils/constants';

const CustomDrawer = createDrawerNavigator();

export const Drawer = () => {
  const { drawerTranslations } = useContext(TranslationsContext);

  return (
    <CustomDrawer.Navigator
      id='main-drawer'
      initialRouteName='PasswordList'
      screenOptions={({ navigation }) => ({
        headerLeft: ({ tintColor }) => (
          <TouchableOpacity onPress={navigation.toggleDrawer}>
            <Feather
              testID='drawer-button'
              name='menu'
              style={{
                marginLeft: 15,
                marginRight: 15,
              }}
              size={24}
              color={tintColor}
            />
          </TouchableOpacity>
        ),
        drawerStyle: {
          width: Platform.OS === 'web' ? 300 : Dimensions.get('window').width * 0.7,
        },
        drawerActiveBackgroundColor: appColors.activeTint,
        drawerActiveTintColor: appColors.primary,
        drawerItemStyle: {
          borderRadius: 5,
        },
      })}
      drawerContent={(sidebarProps: DrawerContentComponentProps) => (
        <CustomSidebar {...sidebarProps} />
      )}
    >
      <CustomDrawer.Screen
        name='PasswordList'
        component={PasswordList}
        options={{
          title: drawerTranslations.t('title'),
          headerStyle: {
            backgroundColor: appColors.primary,
          },
          headerTintColor: appColors.textTint,
          drawerIcon: ({ color, size }) => (
            <Feather testID='password-list-link' name='list' size={size * 0.75} color={color} />
          ),
        }}
      />
    </CustomDrawer.Navigator>
  );
};
