import React, { useContext } from 'react';
import { Text, TextInput, View } from 'react-native';
import { Picker } from '@react-native-picker/picker';
import { Entypo } from '@expo/vector-icons';

import { TranslationsContext } from 'contexts/translationsContext';
import { LoadingIndicator } from 'components/LoadingIndicator/loadingIndicator';
import { appColors } from 'utils/constants';

import stylesShared from 'screens/PasswordGenerator/tailwindStyles';
import stylesRN from './styles';
import styles from './tailwindStyles';
import commonStyles from 'utils/commonStyles';

export const CreateEditPasswordConfigurator = ({
  description,
  passwordDescriptionMaxLen,
  socialMediaPicked,
  socialMediaList,
  isCreateMode,
  createEditButtonLabel,
  handlers,
}: Props) => {
  const { passwordGenTranslations } = useContext(TranslationsContext);
  const { handleOnPress, setDescription, handleOnPickerChange } = handlers;

  if (socialMediaList.length < 1) return <LoadingIndicator />;

  return (
    <View
      className={[stylesShared.cardView.root, styles.root].join(' ')}
      style={[commonStyles.shadow.box]}
    >
      <View className={styles.userInputContainer}>
        <Text className={styles.textLabel}>{passwordGenTranslations.t('socialMedia')}</Text>
        <Picker
          style={stylesRN.dropdown}
          selectedValue={socialMediaPicked}
          onValueChange={handleOnPickerChange}
        >
          {socialMediaList.map((item: { id: string; name: string }) => {
            const label = item.name.charAt(0).toUpperCase() + item.name.slice(1);
            return <Picker.Item key={item.id} label={label} value={item.name} />;
          })}
        </Picker>
      </View>

      <View className={styles.userInputContainer}>
        <Text className={styles.textLabel}>{passwordGenTranslations.t('description')}:</Text>
        <TextInput
          className={styles.textInput}
          style={commonStyles.input.outline}
          placeholder={passwordGenTranslations.t('descriptionPlaceholder')}
          maxLength={passwordDescriptionMaxLen}
          value={description}
          onChangeText={setDescription}
        />
        <Text className={styles.descriptionLengthIndicator}>
          {description.length}/{passwordDescriptionMaxLen}
        </Text>
      </View>

      <Entypo.Button
        style={stylesRN.button}
        name={isCreateMode ? 'save' : 'edit'}
        size={24}
        color={appColors.textTint}
        backgroundColor={appColors.primary}
        onPress={handleOnPress}
      >
        {createEditButtonLabel}
      </Entypo.Button>
    </View>
  );
};

export interface Props {
  description: string;
  passwordDescriptionMaxLen: number;
  socialMediaPicked: string;
  socialMediaList: any;
  isCreateMode: boolean;
  createEditButtonLabel: string;
  handlers: {
    handleOnPress: () => void;
    // eslint-disable-next-line no-unused-vars
    setDescription: (text: string) => void;
    // eslint-disable-next-line no-unused-vars
    handleOnPickerChange: (itemValue: string) => void;
  };
}
