import React, { Dispatch, useContext, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { AnyAction } from '@reduxjs/toolkit';
import { Platform } from 'react-native';
import { resetConfigurationState, showAuthenticatedMessage } from 'utils/configuratorUtils';
import { setSnackbarMessage, setSnackbarVisible } from 'reduxStore/slices/uiElementsSlice';
import {
  resetPasswordPicked,
  selectIsCreateMode,
  selectIsEditMode,
} from 'reduxStore/slices/passwordSlice';
import { selectUserId } from 'reduxStore/slices/userSlice';
import { TranslationsContext } from 'contexts/translationsContext';

const useGeneratorMessage = () => {
  const dispatch: Dispatch<AnyAction> = useDispatch();
  const userId: string = useSelector(selectUserId);
  const isCreateMode: boolean = useSelector(selectIsCreateMode);
  const isEditMode: boolean = useSelector(selectIsEditMode);
  const { infoMessagesTranslations } = useContext(TranslationsContext);

  useEffect(() => {
    if (Platform.OS === 'android') {
      showAuthenticatedMessage(
        userId,
        isCreateMode,
        infoMessagesTranslations.t('about2CreatePassword')
      );
    } else {
      dispatch(
        setSnackbarMessage({ snackbarMessage: infoMessagesTranslations.t('about2CreatePassword') })
      );
      dispatch(setSnackbarVisible({ snackbarVisible: true }));
    }

    (() => isEditMode && dispatch(resetPasswordPicked()))();

    return () => {
      resetConfigurationState(dispatch);
    };
  }, []);

  return true;
};

export default useGeneratorMessage;
