import React from 'react';
import { useSelector } from 'react-redux';
import { Platform, StyleSheet, View } from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { LoadingIndicator } from 'components/LoadingIndicator/loadingIndicator';
import OnBoarding from 'screens/OnBoarding/onBoardingContainer';
import Login from 'screens/Auth/Login/loginContainer';
import SignUp from 'screens/Auth/SignUp/signupContainer';
import PasswordGenerator from 'screens/PasswordGenerator/passwordGeneratorContainer';
import { Drawer } from 'screens/Drawer/drawer';
import CustomStatusbar from 'components/CustomStatusbar/customStatusbar';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import { selectLocale } from 'reduxStore/slices/applicationStatusSlice';

// Translations
import getTranslations from 'utils/getTranslations';
import { TranslationsContext } from 'contexts/translationsContext';
import drawerTranslations from 'languages/drawerTranslations';
import infoMessagesTranslations from 'languages/infoMessagesTranslations';
import loginSignupTranslations from 'languages/loginSignupTranslations';
import modalTranslations from 'languages/modalTranslations';
import onBoardingTranslations from 'languages/onBoardingTranslations';
import passwordGenTranslations from 'languages/passwordGenTranslations';
import passwordListTranslations from 'languages/passwordListTranslations';
import sidebarTranslations from 'languages/sidebarTranslations';

const Stack = createNativeStackNavigator();

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
});

const Root = ({ loading, onBoardingViewed }: RootProps) => {
  const locale = useSelector(selectLocale);
  const translationsContextValue = {
    drawerTranslations: getTranslations(drawerTranslations, locale),
    infoMessagesTranslations: getTranslations(infoMessagesTranslations, locale),
    loginSignupTranslations: getTranslations(loginSignupTranslations, locale),
    modalTranslations: getTranslations(modalTranslations, locale),
    onBoardingTranslations: getTranslations(onBoardingTranslations, locale),
    passwordGenTranslations: getTranslations(passwordGenTranslations, locale),
    passwordListTranslations: getTranslations(passwordListTranslations, locale),
    sidebarTranslations: getTranslations(sidebarTranslations, locale),
  };

  if (onBoardingViewed === null) {
    return null;
  }

  return (
    <TranslationsContext.Provider value={translationsContextValue}>
      <View style={[styles.container]}>
        <CustomStatusbar />
        {loading ? (
          <LoadingIndicator />
        ) : (
          <NavigationContainer>
            <Stack.Navigator>
              {!onBoardingViewed && Platform.OS !== 'web' && (
                <Stack.Screen
                  name='OnBoarding'
                  component={OnBoarding}
                  options={{ headerShown: false }}
                />
              )}
              <Stack.Screen name='Login' component={Login} options={{ headerShown: false }} />
              <Stack.Screen name='SignUp' component={SignUp} options={{ headerShown: false }} />
              <Stack.Screen
                name='PasswordGenerator'
                component={PasswordGenerator}
                options={{
                  headerStyle: {
                    backgroundColor: '#3091e0',
                  },
                  headerTintColor: '#FFF',
                }}
              />
              <Stack.Screen
                name='Tutorial'
                component={OnBoarding}
                options={{ headerShown: false }}
              />
              <Stack.Screen
                navigationKey='main-drawer'
                name='Drawer'
                component={Drawer}
                options={{
                  headerShown: false,
                }}
              />
            </Stack.Navigator>
          </NavigationContainer>
        )}
      </View>
    </TranslationsContext.Provider>
  );
};

export interface RootProps {
  loading: boolean;
  onBoardingViewed: boolean;
}

export default Root;
