import React from 'react';
import { View, FlatList, Animated } from 'react-native';
import { OnBoardingItem } from 'components/OnBoarding/OnBoardingItem/onBoardingItem';
import { Paginator } from 'components/OnBoarding/Paginator/paginator';
import NextButton from 'components/OnBoarding/NextButton/nextButtonContainer';
import { Slide } from 'screens/OnBoarding/slides';

import styles from './tailwindStyles';

export const OnBoarding = ({
  slides,
  currentIndex,
  scrollX,
  slidesRef,
  viewableItemsChanged,
  viewConfig,
  scrollTo,
}: Props) => (
  <View className={styles.root}>
    <FlatList
      ref={slidesRef}
      data={slides}
      renderItem={({ item }) => <OnBoardingItem item={item} />}
      horizontal
      showsHorizontalScrollIndicator={false}
      pagingEnabled
      bounces={false}
      keyExtractor={(item) => `${item.id}`}
      onScroll={Animated.event([{ nativeEvent: { contentOffset: { x: scrollX } } }], {
        useNativeDriver: false,
      })}
      onViewableItemsChanged={viewableItemsChanged}
      viewabilityConfig={viewConfig}
    />
    <View>
      <Paginator data={slides} scrollX={scrollX} />
    </View>
    <View>
      <NextButton scrollTo={scrollTo} percentage={(currentIndex + 1) * (100 / slides.length)} />
    </View>
  </View>
);

export interface Props {
  slides: Array<Slide>;
  currentIndex: number;
  scrollX: Animated.Value;
  slidesRef: React.RefObject<FlatList>;
  viewableItemsChanged: (item: { viewableItems: any }) => void;
  viewConfig: any;
  scrollTo: () => void;
}
