import React, { useContext } from 'react';
import { Text, TextInput, View, TouchableOpacity, TextInputKeyPressEventData } from 'react-native';
import { DrawerNavigationProp } from '@react-navigation/drawer';
import { TranslationsContext } from 'contexts/translationsContext';
import { LoadingIndicator } from 'components/LoadingIndicator/loadingIndicator';
import { CustomPopover } from 'components/Popover/popover';
import { CustomSnackbar } from 'components/CustomSnackbar/customSnackbar';
import { appColors, popoverMessage } from 'utils/constants';
import Icon from 'components/SVGs/Icon';

import stylesRN from 'screens/Auth/styles';
import styles from 'screens/Auth/tailwindStyles';
import commonStyles from 'utils/commonStyles';

export const Login = ({
  navigation,
  username,
  isRequesting,
  snackbarVisible,
  touchableRef,
  handlers,
}: Props) => {
  const { loginSignupTranslations } = useContext(TranslationsContext);
  const { handleOnChangeText, handleLogin, handleOnKeyPress, handleOnDismiss } = handlers;

  if (isRequesting) return <LoadingIndicator />;

  return (
    <View className={[styles.rootWeb, styles.root].join(' ')} style={stylesRN.container}>
      <Icon
        name='PassvaultLogoIcon'
        width={130}
        height={130}
        variant={{ fillColor: appColors.primary }}
      />
      <View className={styles.inputContainer}>
        <TextInput
          className={styles.input}
          testID='username-login-input'
          placeholder={loginSignupTranslations.t('usernamePlaceholder')}
          value={username}
          style={[stylesRN.input, commonStyles.shadow.box]}
          onChangeText={handleOnChangeText}
          onKeyPress={handleOnKeyPress}
        />
      </View>

      <View className={styles.buttonContainer}>
        <TouchableOpacity style={stylesRN.button} testID='login-button' onPress={handleLogin}>
          <Text className={styles.buttonText}>{loginSignupTranslations.t('loginButton')}</Text>
        </TouchableOpacity>

        <TouchableOpacity
          testID='signup-button'
          onPress={() => {
            navigation.navigate('SignUp');
          }}
          style={[stylesRN.button, stylesRN.buttonOutLine]}
        >
          <Text className={styles.buttonOutlineText}>
            {loginSignupTranslations.t('signupButton')}
          </Text>
        </TouchableOpacity>
      </View>

      <View className={styles.linkContainer}>
        <TouchableOpacity
          style={stylesRN.link}
          testID='tooltip-item'
          onPress={() => {
            navigation.navigate('PasswordGenerator');
          }}
        >
          <Text className={styles.linkText}>{loginSignupTranslations.t('tooltip')} </Text>
        </TouchableOpacity>

        <CustomPopover touchableRef={touchableRef} />
      </View>

      <CustomSnackbar
        message={popoverMessage}
        isSnackbarVisible={snackbarVisible}
        onDismiss={handleOnDismiss}
      />
    </View>
  );
};

export interface Props {
  navigation: DrawerNavigationProp<any>;
  username: string;
  isRequesting: boolean;
  snackbarVisible: boolean;
  touchableRef: any;
  handlers: {
    handleOnChangeText: (text: string) => void;
    handleLogin: () => void;
    handleOnKeyPress: ({ nativeEvent }: { nativeEvent: TextInputKeyPressEventData }) => void;
    handleOnDismiss: () => void;
  };
}
