import React, { Dispatch, useContext, useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { AnyAction } from '@reduxjs/toolkit';
import { Clipboard, Platform, ScrollView } from 'react-native';
import { useNavigation } from '@react-navigation/native';
import { DrawerNavigationProp } from '@react-navigation/drawer';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import {
  resetSnackbar,
  selectSnackbarMessage,
  selectSnackbarVisible,
  setModalMessage,
  setModalTitle,
  setModalVisible,
  setSnackbarMessage,
  setSnackbarVisible,
} from 'reduxStore/slices/uiElementsSlice';
import {
  generatePassword,
  selectIsCreateMode,
  selectIsEditMode,
  selectPassword,
  selectPasswordIdPicked,
  selectPasswordPickedWasEncrypted,
  setPassword,
} from 'reduxStore/slices/passwordSlice';
import {
  PasswordGenerator,
  Props as RenderProps,
} from 'screens/PasswordGenerator/passwordGenerator';
import { showInfoMessage } from 'utils/infoMessages';
import useGeneratorMessage from 'hooks/useGeneratorMessage';
import useGeneratorPageTitle from 'hooks/useGeneratorPageTitle';
import { TranslationsContext } from 'contexts/translationsContext';

const PasswordGeneratorContainer = ({ children }: { children: typeof PasswordGenerator }) => {
  const scrollViewRef = useRef<ScrollView>(null);
  const [switchEnabled, setSwitchEnabled] = useState<boolean>(false);
  const [mPassword, mSetPassword] = useState<string>('');
  const { infoMessagesTranslations, modalTranslations } = useContext(TranslationsContext);
  const navigation: DrawerNavigationProp<any> | NativeStackNavigationProp<any> = useNavigation();
  const dispatch: Dispatch<AnyAction> = useDispatch();

  const snackbarMessage: string = useSelector(selectSnackbarMessage);
  const snackbarVisible: boolean = useSelector(selectSnackbarVisible);
  const passwordIdPicked: string = useSelector(selectPasswordIdPicked);
  const passwordFromState: string = useSelector(selectPassword);
  const passwordPickedWasEncrypted: boolean = useSelector(selectPasswordPickedWasEncrypted);
  const isCreateMode: boolean = useSelector(selectIsCreateMode);
  const isEditMode: boolean = useSelector(selectIsEditMode);

  const handleCopyButton = () => {
    if (Platform.OS === 'android') {
      showInfoMessage(infoMessagesTranslations.t('copied2Clipboard'));
    } else {
      dispatch(
        setSnackbarMessage({ snackbarMessage: infoMessagesTranslations.t('copied2Clipboard') })
      );
      dispatch(setSnackbarVisible({ snackbarVisible: true }));
    }

    Clipboard.setString(passwordFromState);
  };

  const handleRefreshButton = () => {
    if (Platform.OS === 'android') {
      showInfoMessage(infoMessagesTranslations.t('newPassword'));
    } else {
      dispatch(setSnackbarMessage({ snackbarMessage: infoMessagesTranslations.t('newPassword') }));
      dispatch(setSnackbarVisible({ snackbarVisible: true }));
    }

    dispatch(generatePassword());
  };

  const handleOnDismissSnackbar = () => {
    dispatch(resetSnackbar());
  };

  const handleOnChangePassword = (text: string) => {
    if (switchEnabled) {
      mSetPassword(text);
    }
  };

  const handleOnBlurPassword = () => {
    if (switchEnabled) {
      dispatch(setPassword({ password: mPassword }));
    }
  };

  const handleSwitch = () => {
    setSwitchEnabled(!switchEnabled);
    if (switchEnabled) {
      mSetPassword('');
      dispatch(generatePassword());
    }
  };

  useEffect(() => {
    if (!passwordPickedWasEncrypted && isEditMode) {
      if (Platform.OS === 'android') {
        alert(modalTranslations.t('encryptPwdMessage'));
      } else {
        dispatch(setModalTitle({ modalTitle: modalTranslations.t('modalTitle') }));
        dispatch(setModalMessage({ modalMessage: modalTranslations.t('encryptPwdMessage') }));
        dispatch(setModalVisible({ modalVisible: true }));
      }
    }
  }, []);

  useGeneratorMessage();
  useGeneratorPageTitle();

  const snackbarObj = { snackbarMessage, snackbarVisible };
  const handlers = {
    handleCopyButton,
    handleRefreshButton,
    handleOnDismissSnackbar,
    handleOnChangePassword,
    handleOnBlurPassword,
    handleSwitch,
  };

  return children({
    navigation,
    scrollViewRef,
    switchEnabled,
    mPassword,
    snackbarObj,
    passwordFromState,
    isCreateMode,
    isEditMode,
    handlers,
  });
};

export default () => (
  <PasswordGeneratorContainer>
    {(renderProps: RenderProps) => <PasswordGenerator {...renderProps} />}
  </PasswordGeneratorContainer>
);
