import React, { useState } from 'react';
import { View, Text, TextInput } from 'react-native';
import { DrawerNavigationProp } from '@react-navigation/drawer';
import { MaterialIcons } from '@expo/vector-icons';

import { SocialMediaIcon } from 'components/SocialMediaIcon/socialMediaIcon';
import PasswordIcons from 'components/PasswordIcons/PasswordIconsContainer';
import { appColors } from 'utils/constants';

import commonStyles from 'utils/commonStyles';
import styles from './tailwindStyles';

export const PasswordItem = ({
  passwordId,
  passwordGenerated,
  socialMedia,
  description,
  pwdWasCorrectlyEncrypted,
  navigation,
}: Props) => {
  const [passwordVisible, setPasswordVisible] = useState(false);

  return (
    <View className={styles.item.root} style={commonStyles.shadow.box}>
      <View className={styles.item.socialIconAndPasswordInfo}>
        <SocialMediaIcon className={styles.icons.socialMedia} socialMedia={socialMedia} />
        <View className={styles.item.passwordAndDescription}>
          <View className={styles.item.passwordTitleWithCryptIcon}>
            <MaterialIcons
              name={pwdWasCorrectlyEncrypted ? 'enhanced-encryption' : 'no-encryption'}
              size={17}
              color={appColors.greyColor}
            />
            <Text className={styles.item.descriptionTextLabel}>{description}</Text>
          </View>
          <TextInput
            className={styles.item.passwordInput}
            style={commonStyles.input.outline}
            showSoftInputOnFocus={false}
            caretHidden
            value={passwordGenerated}
            secureTextEntry={!passwordVisible}
          />
        </View>
      </View>
      <PasswordIcons
        passwordId={passwordId}
        socialMedia={socialMedia}
        description={description}
        passwordGenerated={passwordGenerated}
        passwordVisible={passwordVisible}
        setPasswordVisible={setPasswordVisible}
        navigation={navigation}
      />
    </View>
  );
};

interface Props {
  passwordId: string;
  passwordGenerated: string;
  socialMedia: string;
  description: string;
  pwdWasCorrectlyEncrypted: boolean;
  navigation: DrawerNavigationProp<any>;
}
