// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-nocheck
import { put, select, call, takeLeading } from 'redux-saga/effects';
import { logger } from 'react-native-logs';

import { getPasswordsFromFirebase, setPasswords } from 'reduxStore/slices/passwordSlice';
import { selectUserId } from 'reduxStore/slices/userSlice';
import { unsetIsRequest } from 'reduxStore/slices/applicationStatusSlice';
import { getPasswordsByUserId } from 'services/database';
import decryptMany from 'services/decrypt';
import { PasswordI } from 'utils/constants';
import { getPasswordList, setPasswordList } from 'utils/localStorageFuncs';

const log = logger.createLogger();

// Generator<any, void, SelectEffect | CallEffect>
function* getPasswordsFlow(): Generator {
  try {
    log.info('GetPasswordSaga called');
    const userId: string = yield select(selectUserId);
    const passwordsFromLocalStorage: Array<PasswordI> = yield call(getPasswordList);

    if (passwordsFromLocalStorage.length === 0) {
      const result: Array<PasswordI> = yield call(getPasswordsByUserId, userId);
      const decryptedServiceResult = yield call(
        decryptMany,
        result.map(({ passwordGenerated }) => ({ password: passwordGenerated })),
        userId
      );

      const passwordsDecrypted: Array<{
        password: string;
        isCorrectlyEncrypted: boolean;
      }> = decryptedServiceResult.data.response;

      const passwords: Array<PasswordI> = passwordsDecrypted.map(
        ({ password, isCorrectlyEncrypted }, index) => ({
          id: result[index].id,
          description: result[index].description,
          socialMedia: result[index].socialMedia,
          passwordGenerated: password,
          pwdWasCorrectlyEncrypted: isCorrectlyEncrypted,
        })
      );

      yield call(setPasswordList, passwords);
      yield put(setPasswords({ passwords }));
      yield put(unsetIsRequest());

      return yield;
    }

    yield put(setPasswords({ passwords: passwordsFromLocalStorage }));
    yield put(unsetIsRequest());
  } catch (exception) {
    log.error('GetPasswordSaga: ', { exception: exception.message });
  }
}

export const getPasswordsSaga = [takeLeading(getPasswordsFromFirebase.type, getPasswordsFlow)];
