import React, { useContext } from 'react';
import { View, Text, TextInput, TouchableOpacity, ScrollView } from 'react-native';
import { DrawerNavigationProp } from '@react-navigation/drawer';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import { Divider, Switch } from 'react-native-paper';
import { Ionicons, MaterialCommunityIcons } from '@expo/vector-icons';
import { TranslationsContext } from 'contexts/translationsContext';
import CreateEditPasswordConfigurator from 'components/CreateEditPasswordConfigurator/createEditPwdConfigurationContainer';
import { PasswordConfigurator } from 'components/PasswordConfigurator/passwordConfigurator';
import { CustomSnackbar } from 'components/CustomSnackbar/customSnackbar';
import SliderContainer from 'components/SliderContainer/sliderContainer';
import { appColors } from 'utils/constants';

import commonStyles from 'utils/commonStyles';
import styles from './tailwindStyles';

export const PasswordGenerator = ({
  navigation,
  scrollViewRef,
  switchEnabled,
  mPassword,
  snackbarObj,
  passwordFromState,
  isCreateMode,
  isEditMode,
  handlers,
}: Props) => {
  const { passwordGenTranslations } = useContext(TranslationsContext);
  const { snackbarMessage, snackbarVisible } = snackbarObj;
  const {
    handleCopyButton,
    handleRefreshButton,
    handleOnDismissSnackbar,
    handleOnChangePassword,
    handleOnBlurPassword,
    handleSwitch,
  } = handlers;

  return (
    <View className={styles.rootWeb}>
      <ScrollView ref={scrollViewRef}>
        <View className={styles.container}>
          <View
            className={[styles.cardView.root, styles.password.root].join(' ')}
            style={[commonStyles.shadow.box]}
          >
            <View className={styles.password.inputContainer}>
              <TextInput
                className={styles.password.input}
                style={commonStyles.input.outline}
                showSoftInputOnFocus={switchEnabled}
                caretHidden={!switchEnabled}
                value={switchEnabled ? mPassword : passwordFromState}
                onChangeText={handleOnChangePassword}
                onBlur={handleOnBlurPassword}
              />
              <View className={styles.password.icons}>
                <TouchableOpacity onPress={handleCopyButton}>
                  <MaterialCommunityIcons
                    style={{ marginRight: 10 }}
                    name='content-copy'
                    size={24}
                    color='grey'
                  />
                </TouchableOpacity>
                <TouchableOpacity onPress={handleRefreshButton}>
                  <Ionicons name='reload' size={24} color='grey' />
                </TouchableOpacity>
              </View>
            </View>
          </View>

          <View
            className={[styles.cardView.root, styles.configuration.root].join(' ')}
            style={commonStyles.shadow.box}
          >
            <View className={styles.configuration.textContainer}>
              <Text className={styles.configuration.textHeader}>
                {passwordGenTranslations.t('heading')}
              </Text>
            </View>

            <Divider style={{ backgroundColor: 'grey' }} />

            {isCreateMode && (
              <>
                <View className={styles.configuration.switchToggle}>
                  <Text className={styles.configuration.textLabel}>
                    {passwordGenTranslations.t('placeOwnPassword')}
                  </Text>
                  <Switch
                    value={switchEnabled}
                    onValueChange={handleSwitch}
                    color={appColors.primary}
                  />
                </View>

                <Divider style={{ backgroundColor: 'grey' }} />
              </>
            )}

            <View className={styles.configuration.passwordLengthContainer}>
              <Text className={styles.configuration.textLabel}>
                {passwordGenTranslations.t('passwordLength')}
              </Text>
              <View className={styles.configuration.lengthSliderContainer}>
                <TextInput
                  className={styles.configuration.inputLength}
                  style={commonStyles.input.outline}
                  showSoftInputOnFocus={false}
                  caretHidden
                  keyboardType='numeric'
                  value={passwordFromState.length.toString()}
                />
                <SliderContainer
                  switchEnabled={switchEnabled}
                  defaultValue={isEditMode ? passwordFromState.length : 10}
                />
              </View>
            </View>

            <Divider style={{ backgroundColor: 'grey' }} />

            <PasswordConfigurator switchEnabled={switchEnabled} />
          </View>

          {(isCreateMode || isEditMode) && <CreateEditPasswordConfigurator />}
        </View>
      </ScrollView>

      <CustomSnackbar
        message={snackbarMessage}
        isSnackbarVisible={snackbarVisible}
        onDismiss={handleOnDismissSnackbar}
      />
    </View>
  );
};

export interface Props {
  navigation: DrawerNavigationProp<any> | NativeStackNavigationProp<any>;
  scrollViewRef: React.RefObject<ScrollView>;
  switchEnabled: boolean;
  mPassword: string;
  snackbarObj: {
    snackbarMessage: string;
    snackbarVisible: boolean;
  };
  passwordFromState: string;
  isCreateMode: boolean;
  isEditMode: boolean;
  handlers: {
    handleCopyButton: () => void;
    handleRefreshButton: () => void;
    handleOnDismissSnackbar: () => void;
    handleOnChangePassword: (text: string) => void;
    handleOnBlurPassword: () => void;
    handleSwitch: () => void;
  };
}
