import React, { Dispatch } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { AnyAction } from '@reduxjs/toolkit';
import { Platform } from 'react-native';
import { useNavigation } from '@react-navigation/native';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import { DrawerContentComponentProps } from '@react-navigation/drawer';
import { DrawerNavigationHelpers } from '@react-navigation/drawer/lib/typescript/src/types';
import { selectUserEmail } from 'reduxStore/slices/userSlice';
import { setIsCreateMode, unsetPasswords } from 'reduxStore/slices/passwordSlice';
import { CustomSidebar, Props as RenderProps } from 'components/CustomSidebar/customSidebar';
import { signOut } from 'services/authFirebase';

const CustomSidebarContainer = ({
  sidebarProps,
  children,
}: {
  sidebarProps: DrawerContentComponentProps;
  children: typeof CustomSidebar;
}) => {
  const userEmail = useSelector(selectUserEmail);
  const name: string = userEmail ? userEmail.substring(0, userEmail.indexOf('@')) : '';
  const userName: string = name.charAt(0).toUpperCase() + name.slice(1);
  const stackNavigation = useNavigation<NativeStackNavigationProp<any>>();
  const dispatch: Dispatch<AnyAction> = useDispatch();

  const handleSignOut = () => {
    signOut().then(() => {
      dispatch(unsetPasswords());
      stackNavigation.replace('Login');
    });
  };

  const onPressInPasswordGenerator = (navigation: DrawerNavigationHelpers) => {
    navigation.closeDrawer();
    dispatch(setIsCreateMode({ isCreateMode: true }));
    navigation.navigate('PasswordGenerator');
  };

  const onPressInCheckTutorial = (navigation: DrawerNavigationHelpers) => {
    if (Platform.OS === 'web') {
      alert('This is not available in web. Please, stay tuned.');
    } else {
      navigation.navigate('Tutorial');
    }
    navigation.closeDrawer();
  };
  const handlers = {
    handleSignOut,
    onPressInPasswordGenerator,
    onPressInCheckTutorial,
  };

  return children({
    sidebarProps,
    userName,
    handlers,
  });
};

export default (sidebarProps: DrawerContentComponentProps) => (
  <CustomSidebarContainer sidebarProps={sidebarProps}>
    {(renderProps: RenderProps) => <CustomSidebar {...renderProps} />}
  </CustomSidebarContainer>
);

