import React from 'react';
import { Text, View } from 'react-native';
import { MaterialIcons } from '@expo/vector-icons';
import Modal from 'react-native-modal';
import { appColors } from 'utils/constants';

import styles from './tailwindStyles';

export const CustomModal = (props: {
  visible: boolean;
  toggle: any;
  onSubmit: any;
  title: string;
  message: string;
  noLabel: string;
  yesLabel: string;
}) => {
  const { visible, toggle, onSubmit, title, message, noLabel, yesLabel } = props;

  return (
    <Modal
      testID='modal-window'
      isVisible={visible}
      onBackdropPress={toggle}
      onBackButtonPress={toggle}
    >
      <View className={styles.root}>
        <Text className={styles.modalTitle}>{title}</Text>
        <Text className={styles.modalMessage}>{message}</Text>
        <View className={styles.buttonsContainer}>
          <View className={styles.button}>
            <MaterialIcons.Button
              testID='modal-no-button'
              name='close'
              onPress={toggle}
              backgroundColor={appColors.red}
            >
              {noLabel}
            </MaterialIcons.Button>
          </View>
          <View className={styles.button}>
            <MaterialIcons.Button
              testID='modal-yes-button'
              name='check'
              onPress={onSubmit}
              backgroundColor={appColors.primary}
            >
              {yesLabel}
            </MaterialIcons.Button>
          </View>
        </View>
      </View>
    </Modal>
  );
};
