/* eslint-disable no-undef */
import axios from 'axios';

const decryptMany = (passwords: Array<{ password: string }>, userId: string) => {
  const isLocalAPI = process.env.AWS_LOCAL_API_ENABLED === 'true';
  const apiURL = isLocalAPI ? process.env.AWS_LOCAL_API_URL : process.env.AWS_API_URL;

  return axios.post(
    `${apiURL}/decryptMany`,
    {
      userId,
      passwords,
    },
    {
      headers: {
        'Content-Type': 'application/json',
      },
    }
  );
};

export default decryptMany;
