import React from 'react';
import { TouchableOpacity, View, Text } from 'react-native';
import { Entypo, FontAwesome, Ionicons, MaterialCommunityIcons } from '@expo/vector-icons';
import MenuList, { MenuItem } from 'components/MenuList';
import { appColors } from 'utils/constants';
import styles from 'components/PasswordItem/tailwindStyles';

export const PasswordIcons = ({ passwordVisible, handlers }: Props) => {
  const { handleShowHidePassword, handleCopyButton, handleEditButton, handleDeleteButton } =
    handlers;

  return (
    <View className={styles.icons.root}>
      <TouchableOpacity onPress={handleShowHidePassword}>
        <Entypo name={passwordVisible ? 'eye' : 'eye-with-line'} size={18} color='grey' />
      </TouchableOpacity>

      <TouchableOpacity onPress={handleCopyButton}>
        <MaterialCommunityIcons name='content-copy' size={18} color='grey' />
      </TouchableOpacity>

      <MenuList>
        <MenuItem onPress={handleEditButton}>
          <Text>Edit </Text>
          <FontAwesome name='edit' size={18} color='grey' />
        </MenuItem>
        <MenuItem onPress={handleDeleteButton}>
          <Text>Delete</Text>
          <Ionicons name='trash' size={18} color={appColors.red} />
        </MenuItem>
      </MenuList>
    </View>
  );
};

export interface Props {
  passwordVisible: boolean;
  handlers: {
    handleShowHidePassword: () => void;
    handleCopyButton: () => void;
    handleEditButton: () => void;
    handleDeleteButton: () => void;
  };
}
