import React, { useContext, useLayoutEffect } from 'react';
import { useSelector } from 'react-redux';
import { selectIsEditMode } from 'reduxStore/slices/passwordSlice';
import { selectUserId } from 'reduxStore/slices/userSlice';
import { useNavigation } from '@react-navigation/native';
import { TranslationsContext } from '../contexts/translationsContext';

const useGeneratorPageTitle = () => {
  const navigation = useNavigation();
  const userId: string = useSelector(selectUserId);
  const isEditMode: boolean = useSelector(selectIsEditMode);
  const { passwordGenTranslations } = useContext(TranslationsContext);

  const screenTitle = isEditMode
    ? passwordGenTranslations.t('editTitle')
    : `${passwordGenTranslations.t('generatorTitle')} ${
        !userId ? passwordGenTranslations.t('isEdit') : ''
      }`;

  useLayoutEffect(() => {
    navigation.setOptions({
      title: screenTitle,
    });
  });

  return true;
};

export default useGeneratorPageTitle;
