/* eslint-disable no-undef */
import axios from 'axios';

const encrypt = (password: string, userId: string) => {
  const isLocalAPI = process.env.AWS_LOCAL_API_ENABLED === 'true';
  const apiURL = isLocalAPI ? process.env.AWS_LOCAL_API_URL : process.env.AWS_API_URL;

  return axios.post(
    `${apiURL}/encrypt`,
    {
      userId,
      password,
    },
    {
      headers: {
        'Content-Type': 'application/json',
      },
    }
  );
};

export default encrypt;
