import React from 'react';
import { Image, ImageSourcePropType, StyleProp } from 'react-native';

import { AntDesign } from '@expo/vector-icons';

import {
  imageSourceRetriever,
  socialMediaIcon,
  socialMediaIconColor,
} from 'utils/imageDataRetriever';

export const SocialMediaIcon = ({ className, style, socialMedia }: Props) => {
  if (['amazon', 'discord', 'google', 'slack', 'okta', 'outlook', 'twitch'].includes(socialMedia)) {
    const imageSource = imageSourceRetriever(socialMedia);

    return (
      <Image className={className} style={style} source={imageSource as ImageSourcePropType} />
    );
  }
  const getSocialMedia: any = socialMediaIcon(socialMedia);
  const socialMediaColor = socialMediaIconColor(socialMedia);

  return (
    <AntDesign
      className={className}
      style={style}
      name={getSocialMedia}
      size={24}
      color={socialMediaColor}
    />
  );
};

interface Props {
  className: string;
  style?: StyleProp<never>;
  socialMedia: string;
}
