import React, { useRef, useState } from 'react';
import { Pressable, TouchableOpacity, View } from 'react-native';
import OutsidePressHandler from 'react-native-outside-press';
import { Entypo } from '@expo/vector-icons';

import commonStyles from 'utils/commonStyles';
import styles from './tailwindStyles';

export const MenuItem = ({ onPress, children }: MenuItemsProps) => (
  <TouchableOpacity onPress={onPress}>
    <View className={styles.menuItem}>{children}</View>
  </TouchableOpacity>
);

interface MenuItemsProps {
  onPress: () => void;
  children: React.ReactNode;
}

export default ({ children }: Props) => {
  const [visible, setVisible] = useState(false);
  const ref = useRef(null);

  const handleOnOutsidePress = () => visible && setVisible(false);

  return (
    <OutsidePressHandler onOutsidePress={handleOnOutsidePress} disabled={!visible}>
      <Pressable
        style={{
          position: 'relative',
        }}
        onPress={() => setVisible(!visible)}
      >
        <Entypo name='dots-three-vertical' size={18} color='grey' />
        {visible && (
          <View ref={ref} className={styles.menuItemRoot} style={commonStyles.shadow.box}>
            {children}
          </View>
        )}
      </Pressable>
    </OutsidePressHandler>
  );
};

interface Props {
  children: React.ReactNode;
}
