import React, { useContext } from 'react';
import { View, Text, TextInput, TouchableOpacity, TextInputKeyPressEventData } from 'react-native';
import { NavigationProp } from '@react-navigation/native';
import { TranslationsContext } from 'contexts/translationsContext';
import Icon from 'components/SVGs/Icon';

import { appColors } from 'utils/constants';
import stylesRN from 'screens/Auth/styles';
import styles from 'screens/Auth/tailwindStyles';
import commonStyles from 'utils/commonStyles';

export const SignUp = ({ navigation, username, handlers }: Props) => {
  const { loginSignupTranslations } = useContext(TranslationsContext);
  const { handleSignUp, handleOnChangeText, handleOnKeyPress } = handlers;

  return (
    <View className={[styles.rootWeb, styles.root].join(' ')} style={stylesRN.container}>
      <Icon
        name='PassvaultLogoIcon'
        width={130}
        height={130}
        variant={{ fillColor: appColors.primary }}
      />
      <View className={styles.inputContainer}>
        <TextInput
          className={styles.input}
          placeholder={loginSignupTranslations.t('usernamePlaceholder')}
          value={username}
          style={[stylesRN.input, commonStyles.shadow.box]}
          onChangeText={handleOnChangeText}
          onKeyPress={handleOnKeyPress}
        />
      </View>

      <View style={stylesRN.buttonContainer}>
        <TouchableOpacity onPress={handleSignUp} style={stylesRN.button}>
          <Text className={styles.buttonText}>{loginSignupTranslations.t('signupButton')}</Text>
        </TouchableOpacity>
      </View>

      <View className={styles.linkContainer}>
        <TouchableOpacity onPress={() => navigation.navigate('Login')} style={stylesRN.link}>
          <Text className={styles.linkText}>{loginSignupTranslations.t('goBack2Login')}</Text>
        </TouchableOpacity>
      </View>
    </View>
  );
};

export interface Props {
  navigation: NavigationProp<any>;
  username: string;
  handlers: {
    handleSignUp: () => void;
    handleOnChangeText: (text: string) => void;
    handleOnKeyPress: ({ nativeEvent }: { nativeEvent: TextInputKeyPressEventData }) => void;
  };
}
