import React, { Dispatch, useContext } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { AnyAction } from '@reduxjs/toolkit';
import { Clipboard, Platform } from 'react-native';
import { DrawerNavigationProp } from '@react-navigation/drawer';

import { TranslationsContext } from 'contexts/translationsContext';
import {
  selectPasswords,
  setIsDeleteMode,
  setIsEditMode,
  setPassword,
  setPasswordDescriptionPicked,
  setPasswordIdPicked,
  setPasswordPicked,
  setPasswordPickedWasEncrypted,
} from 'reduxStore/slices/passwordSlice';
import {
  setModalMessage,
  setModalTitle,
  setModalVisible,
  setSnackbarMessage,
  setSnackbarVisible,
} from 'reduxStore/slices/uiElementsSlice';
import { setSocialMediaPicked } from 'reduxStore/slices/socialMediaSlice';
import { PasswordI } from 'utils/constants';
import { showInfoMessage } from 'utils/infoMessages';

import { PasswordIcons } from './passwordIcons';
import { Props as RenderProps } from './passwordIcons';

const PasswordIconsContainer = ({
  passwordId,
  socialMedia,
  description,
  passwordGenerated,
  passwordVisible,
  setPasswordVisible,
  navigation,
  children,
}: ContainerProps) => {
  const dispatch: Dispatch<AnyAction> = useDispatch();
  const { infoMessagesTranslations, modalTranslations } = useContext(TranslationsContext);
  const mSocialMedia: string = socialMedia.charAt(0).toUpperCase() + socialMedia.slice(1);
  const passwords: Array<PasswordI> = useSelector(selectPasswords);

  const handleShowHidePassword = () => {
    setPasswordVisible(!passwordVisible);
  };

  const handleCopyButton = () => {
    if (Platform.OS === 'android') {
      showInfoMessage(infoMessagesTranslations.t('copied2Clipboard'));
    } else {
      dispatch(
        setSnackbarMessage({ snackbarMessage: infoMessagesTranslations.t('copied2Clipboard') })
      );
      dispatch(setSnackbarVisible({ snackbarVisible: true }));
    }

    Clipboard.setString(passwordGenerated);
  };

  const handleEditButton = () => {
    passwords.map(({ id, pwdWasCorrectlyEncrypted }) => {
      if (id === passwordId) {
        dispatch(
          setPasswordPickedWasEncrypted({ passwordPickedWasEncrypted: pwdWasCorrectlyEncrypted })
        );
      }
    });

    dispatch(setIsEditMode({ isEditMode: true }));
    dispatch(setPasswordIdPicked({ passwordIdPicked: passwordId }));
    dispatch(setPasswordPicked({ passwordPicked: passwordGenerated }));
    dispatch(setPassword({ password: passwordGenerated }));
    dispatch(setPasswordDescriptionPicked({ passwordDescriptionPicked: description }));
    dispatch(setSocialMediaPicked({ socialMediaPicked: socialMedia }));
    navigation.navigate('PasswordGenerator');
  };

  const handleDeleteButton = () => {
    dispatch(setIsDeleteMode({ isDeleteMode: true }));
    dispatch(setPasswordIdPicked({ passwordIdPicked: passwordId }));
    dispatch(setModalTitle({ modalTitle: modalTranslations.t('deleteModalTitle') }));
    dispatch(
      setModalMessage({
        modalMessage: modalTranslations.t('deleteModalMessage').replace('PLATFORM', mSocialMedia),
      })
    );
    dispatch(setModalVisible({ modalVisible: true }));
  };

  return children({
    passwordVisible,
    handlers: { handleShowHidePassword, handleCopyButton, handleEditButton, handleDeleteButton },
  });
};

export default (props: InitialProps) => (
  <PasswordIconsContainer {...props}>
    {(renderProps: RenderProps) => <PasswordIcons {...renderProps} />}
  </PasswordIconsContainer>
);

interface InitialProps {
  passwordId: string;
  socialMedia: string;
  description: string;
  passwordGenerated: string;
  passwordVisible: boolean;
  setPasswordVisible: any;
  navigation: DrawerNavigationProp<any>;
}

interface ContainerProps extends InitialProps {
  children: typeof PasswordIcons;
}
