/* eslint-disable no-undef */
import React from 'react';

import { LogBox } from 'react-native';
import { logger } from 'react-native-logs';
import { Provider } from 'react-redux';
import { TailwindProvider } from 'tailwindcss-react-native';
import { EventProvider } from 'react-native-outside-press';

import store from 'reduxStore/store';
import { devWarnings } from 'utils/constants';
import Root from './src/root/rootContainer';

if (process.env.ENV === 'dev') {
  const log = logger.createLogger();
  const isLocalAPI = process.env.AWS_LOCAL_API_ENABLED === 'true';
  const passvaultAPI = isLocalAPI ? process.env.AWS_LOCAL_API_URL : process.env.AWS_API_URL;

  LogBox.ignoreLogs(devWarnings);
  log.info(`Is local API: ${isLocalAPI}`);
  log.info(`Using Passvault-API at ${passvaultAPI}`);
}

declare global {
  // eslint-disable-next-line no-unused-vars
  interface Window {
    store: any;
  }
}

window.store = store;

const App = () => (
  <Provider store={store}>
    <TailwindProvider>
      <EventProvider style={{ flex: 1 }}>
        <Root />
      </EventProvider>
    </TailwindProvider>
  </Provider>
);

export default App;
