// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-nocheck
import { select, call, takeLeading } from 'redux-saga/effects';
import { logger } from 'react-native-logs';

import { savePassword2Firebase } from 'reduxStore/slices/passwordSlice';
import { selectUserId } from 'reduxStore/slices/userSlice';
import { sendPassword2Firebase } from 'services/database';
import encrypt from 'services/encrypt';
import { setPasswordList } from 'utils/localStorageFuncs';

const log = logger.createLogger();

function* savePasswordFlow({ payload }: Params): Generator {
  try {
    log.info('SavePasswordSaga called');
    const userId: string = yield select(selectUserId);
    const { socialMedia, password, description } = payload;
    const encryptServiceResult = yield call(encrypt, password, userId);
    const encryptedPassword = encryptServiceResult.data.response.password;

    yield call(setPasswordList, []);
    yield call(sendPassword2Firebase, userId, encryptedPassword, description, socialMedia);
  } catch (exception) {
    log.error('SavePasswordSaga: ', { exception: exception.message });
  }
}

export const savePasswordSaga = [takeLeading(savePassword2Firebase.type, savePasswordFlow)];

interface Params {
  payload: {
    socialMedia: string;
    password: string;
    description: string;
  };
}
