// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-nocheck
import { select, call, takeLeading, put } from 'redux-saga/effects';
import { logger } from 'react-native-logs';

import {
  editPasswordFromFirebase,
  selectPassword,
  selectPasswordDescriptionPicked,
  selectPasswordIdPicked,
} from 'reduxStore/slices/passwordSlice';
import { updatePasswordByIdFromFirebase } from 'services/database';
import { selectSocialMediaPicked } from 'reduxStore/slices/socialMediaSlice';
import { setIsRequest, unsetIsRequest } from 'reduxStore/slices/applicationStatusSlice';
import { selectUserId } from 'reduxStore/slices/userSlice';
import encrypt from 'services/encrypt';
import { setPasswordList } from 'utils/localStorageFuncs';

const log = logger.createLogger();

function* updatePasswordFlow(): Generator {
  try {
    log.info('UpdatePasswordSaga called');
    const userId: string = yield select(selectUserId);
    const passwordId: string = yield select(selectPasswordIdPicked);
    const socialMedia: string = yield select(selectSocialMediaPicked);
    const password: string = yield select(selectPassword);
    const description: string = yield select(selectPasswordDescriptionPicked);

    const encryptServiceResult = yield call(encrypt, password, userId);
    const encryptedPassword = encryptServiceResult.data.response.password;

    yield call(setPasswordList, []);
    yield put(setIsRequest());
    yield call(
      updatePasswordByIdFromFirebase,
      passwordId,
      encryptedPassword,
      description,
      socialMedia
    );
    yield put(unsetIsRequest());
  } catch (exception) {
    log.error('UpdatePasswordSaga: ', { exception: exception.message });
  }
}

export const updatePasswordSaga = [takeLeading(editPasswordFromFirebase.type, updatePasswordFlow)];
